.counter {
  text-align: center;
  width: 100%;
  margin-top: 50px;
  color: blue;
  border: 3px solid rgb(48, 45, 45);
  border-radius: 20px;
}

.counter:hover {
  box-shadow: 0px 15px 10px -7px rgb(252, 5, 87);
  transform: scale(1.05, 1.05);
}

.counter h2 {
  margin: 50px;
  color: rgb(255, 0, 0);
}

.btn {
  text-align: center;
  margin: 10px;
  font-weight: bold;
  font-size: 18px;
  border: 3px solid rgb(43, 255, 0);
}

.btn:hover {
  border-top: 2px solid blue;
  box-shadow: 0px 10px 10px 0px rgb(54, 254, 4);
  transform: scale(1.05, 1.05);
}

span {
  color: rgb(248, 78, 5);
}

.center {
  text-align: center;
  padding-top: 20px;
  width: 100%;
  height: 350px;
  background-color: rgb(200, 246, 246);
  color: blue;
}

h2 {
  text-align: center;
  color: red;
}

input {
  font-size: 18px;
  color: blue;
  font-family: sans-serif;
  font-weight: 600;
}

